<template>
  <div class="listBox">     
    <h2>List</h2>
    <transition-group name="list" tag="ul">
        <li v-for="(todoItem,index) in propsdata" :key="todoItem">
            {{todoItem}}
            <span class="btn" type="button" @click="removeTodo(todoItem, index)">
                <img src="https://cdn4.iconfinder.com/data/icons/ionicons/512/icon-ios7-minus-empty-256.png" />
            </span>
        </li>
    </transition-group>    
  </div>
</template>

<script>
export default {
  props: ['propsdata'],
  methods: {
    removeTodo(todoItem, index) {
      this.$emit('removeTodo', todoItem, index);
    }
  }
}
</script>

<style>

</style>


