<template>
    <div>
        <div class="inputBox">
            <input type="text" v-model="newTodoItem"  placeholder=" ." v-on:keypress.enter="addTodo"/>
            <span class="btn" @click="addTodo">
                <img src="https://cdn4.iconfinder.com/data/icons/ionicons/512/icon-ios7-plus-empty-256.png" />
            </span>        
        </div>

        <modal v-if="showModal" @close="showModal = false">
            <h3 slot="header"></h3>
            <span slot="footer" @click="showModal = false">  .
                <span class="btn"></span>
            </span>
        </modal>
    </div>    
</template>

<script>

import Modal from './common/Modal.vue'

export default {
    data(){
        return{
            newTodoItem : '',
            showModal: false
        }
    },
    methods : {
        addTodo() {            
            if (this.newTodoItem !== "") {
                var value = this.newTodoItem && this.newTodoItem.trim();
				this.$emit('addTodo', value)
                this.clearInput();
            }else {
                this.showModal = !this.showModal
            }
        },
        clearInput() {
            this.newTodoItem = '';
        }
    },
    components : {
        Modal
    }

}
</script>

<style>

</style>


