<template>
  <div id="app" class="wrapper">
    <TodoHeader></TodoHeader>
    <TodoInput v-on:addTodo="addTodo"></TodoInput>
    <TodoList v-bind:propsdata="todoItems" @removeTodo="removeTodo"></TodoList>
    <TodoFooter v-on:removeAll="clearAll"></TodoFooter>
  </div>
</template>

<script>
import TodoHeader from './components/TodoHeader.vue';
import TodoInput from './components/TodoInput.vue';
import TodoList from './components/TodoList.vue';
import TodoFooter from './components/TodoFooter.vue';

export default {
  data() {
    return {
      todoItems: []
    }
  },
  methods: {
    clearAll() {
      localStorage.clear();
      this.todoItems = [];
    },
		addTodo(todoItem) {
			localStorage.setItem(todoItem, todoItem);
			this.todoItems.push(todoItem);
		},
    removeTodo(todoItem, index) {
      localStorage.removeItem(todoItem);
      this.todoItems.splice(index, 1);
    }
  },
  created() {
		if (localStorage.length > 0) {
			for (var i = 0; i < localStorage.length; i++) {
				this.todoItems.push(localStorage.key(i));
			}
		}
  },
  components: {
    'TodoHeader': TodoHeader,
    'TodoInput': TodoInput,
    'TodoList': TodoList,
    'TodoFooter': TodoFooter
  }
}
</script>

<style>
html,body,h1,input,select {
	font-family:'Do Hyeon',sans-serif;
}
body,h1 {
	font-size:16px;
}
h1 {
	font-weight:normal;
	margin:0;
	padding:0
}
h3 {
	font-weight:normal;
	margin:0;
	padding:0;
	font-size:20px;
	line-height:1.3
}
body {
	margin:0;
	background:#fff;
	color:#333;
}
a {
	color:#1a0dab;
	text-decoration:none;
	-webkit-tap-highlight-color:rgba(0,0,0,.10)
}
a:visited {
	color:#609
}
a:hover {
	text-decoration:underline
}
cite,cite a:link,cite a:visited {
	color:#202124;
	font-style:normal
}
button {
	margin:0
}
ol li {
	list-style:none
}
ol,ul,li {
	margin:0;
	padding:0
}
input {
	font-size:18px;
	border:none;
	background:transparent
}
em {
	font-weight:bold;
	font-style:normal
}
.wrapper {
	margin:35px 25px;
}
.wrapper h1 {
	font-size:42px;
	font-weight:bold;
	;
	margin-block:20px;
	;
}
.wrapper .inputBox {
	position:relative;
	width:100%;
	height:50px;
	background-color:#ecf1f5;
	border-radius:20px;
	color:#7f838c;
}
.wrapper .inputBox input {
	padding:15px;
  width:calc(100% - 50px)
}
.wrapper .inputBox input:focus {
  outline: none;
}
.wrapper .inputBox .btn {
	position:absolute;
	top:50%;
	right:15px;
	transform:translate(0,-50%);
	width:30px;
	height:30px;
	background-color:#407eb6;
	border-radius:50px;
}
.wrapper .inputBox .btn img {
	width:30px;
	filter:invert(1)
}
.wrapper h2 {
	font-size:24px;
	padding-top:20px;
	border-top:1px solid #eee;
}
.wrapper ul li {
	position:relative;
	display:block;
	margin:12px 0;
	box-shadow:2px 2px 6px 1px #cdcdcd;
	border-radius:10px;
	width:100%;
	height:50px;
	line-height:50px;
	text-indent:20px;
	color:#777;
}
.wrapper ul li .btn {
	position:absolute;
	top:50%;
	right:20px;
	transform:translate(0,-50%);
	width:30px;
	height:30px;
	background-color:#407eb6;
	border-radius:50px;
}
.wrapper ul li .btn img {
	width:30px;
	filter:invert(1);
	float:left;
}
.wrapper .clearBox {
	text-align:right;
}
.wrapper .clearBox .btn {
	display:inline-block;
	padding:8px 16px;
	border-radius:10px;
	color:#777;
	box-shadow:2px 2px 6px 1px #cdcdcd;
	font-size:14px;
}
.list-enter-active,.list-leave-active {
	transition:all 1s;
}
.list-enter,.list-leave-to {
	opacity:0;
	transform:translateY(30px);
}

.modal-mask {
	position:fixed;
	z-index:9998;
	top:0;
	left:0;
	width:100%;
	height:100%;
	background-color:rgba(0,0,0,.5);
	display:table;
	transition:opacity .3s ease;
}
.modal-wrapper {
	display:table-cell;
	vertical-align:middle;
}
.modal-container {
	width:70%;
	margin:0px auto;
	padding:20px 30px 10px;
	background-color:#fff;
	border-radius:2px;
	box-shadow:0 2px 8px rgba(0,0,0,.33);
	transition:all .3s ease;
  border-radius: 20px;
}
.modal-header h3 {
  font-size: 28px;
	margin-top:0;
  margin-block: 15px;;
	color:#407eb6;
}
.modal-body {
	margin:20px 0;
}
.modal-container .btn {
  width:30px;
	display:block;
  margin:35px auto 15px;
	padding:8px 16px;
	border-radius:10px;
	color:#777;
	box-shadow:2px 2px 6px 1px #cdcdcd;
	font-size:14px;
  text-align: center;
}
.modal-default-button {
	float: right;
}
.modal-enter {
	opacity:0;
}
.modal-leave-active {
	opacity:0;
}
.modal-enter .modal-container,.modal-leave-active .modal-container {
	-webkit-transform:scale(1.1);
	transform: scale(1.1);
}
</style>




